import React, { Component } from 'react';
import 'react-widgets/dist/css/react-widgets.css';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';
import {Combobox} from 'react-widgets';

class Forms extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      driverLicenseNo:'',driverContact:'',driverName:'',drivers:[],
      btnText:'Save',btnColor:'dark',liceStatus:false,
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount(){
    this.getDrivers();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }

 onSubmit(e){
    e.preventDefault();
    if(!this.state.btnText.localeCompare('Save')){
      this.addLorry();
    }
    else{
      this.updateLorry();
    }
    
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }
  //fetching drivers from database
  getDrivers(){
    fetch('/getdrivers')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        drivers : json
      });
    });
  }
  //adding a new driver  to database
  adddriver= async()=>{
    await axios.post('/adddriver',{
        driverLicenseNo:this.state.driverLicenseNo,
        driverName:this.state.driverName,
        driverContact:this.state.driverContact
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('Successfull')){
        alert('Successfully Added');
        this.getDrivers();
      }
      else{
        alert('Bad Credentials')
      }
    })
  }
//Update a driver
updateDriver= async()=>{
  await axios.post('/updatedriver',{
    driverLicenseNo:this.state.driverLicenseNo,
    driverName:this.state.driverName,
    driverContact:this.state.driverContact
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('Successfull')){
      alert('Successfully Updated');
      this.getDrivers();
    }
    else{
      alert('Bad Credentials')
    }
  })
}

//event handlers for lorry updates and deletes
  onRowSelect=(rowKeys)=>{
this.setState({
    driverLicenseNo:rowKeys.driverLicenseNo,
    driverContact:rowKeys.driverContact,
    driverName:rowKeys.driverName,
    btnText:'Update',
    btnColor:'info',
    liceStatus:true,
    })
  }

 onAfterDeleteRow=(rowKeys)=> {
  axios.post('/deletedriver',{
    driverLicenseNo:rowKeys
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('Successfull')){
      alert('Successfully Deleted');
      this.getDrivers();
    }
    else{
      alert('Failed')
    }
  })
    
  }
  
  options = {
    afterDeleteRow: this.onAfterDeleteRow  // A hook for after droping rows.
  };
  
  selectRowProp = {
    mode: 'radio',
    onSelect: this.onRowSelect,
    bgColor:'yellow'
  };

  //event handler for clear button
  clearForm=()=>{
    this.setState({
      driverLicenseNo:'',
      driverContact:'',
      driverName:'',
      btnText:'Save',
      btnColor:'dark',
      liceStatus:false
    })
  }
  //event handler for save button
  handleSave=()=>{
    if(!this.state.btnText.localeCompare('Save')){
      this.adddriver();
    }
    else{
      this.updateDriver();
    }
  }
  render() {
    return (
      <div className="animated fadeIn">
         <Container>
        <Row>
          <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong>Drivers</strong>
              </CardHeader>
              <CardBody>
                <Form onSubmit={this.onSubmit}>
              <FormGroup row>
                  </FormGroup>
                <FormGroup>
                <Label htmlFor="name">Driver Name</Label>
                  <Input type="text" name="driverName"   value={this.state.driverName} onChange={this.onChange} />  
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="liscence">Liscence No</Label>
                  <Input type="text" name="driverLicenseNo" disabled={this.state.liceStatus}  value={this.state.driverLicenseNo} onChange={this.onChange} />
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="contact">Contact</Label>
                  <Input type="text" name="driverContact" value={this.state.driverContact} onChange={this.onChange}/>
                </FormGroup>
                  <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Button block color={this.state.btnColor} onClick={this. handleSave}>{this.state.btnText}</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={this.clearForm}>clear</Button>
                    </FormGroup>
                  </Col>
                  </FormGroup>
                  </Form>
              </CardBody>
            </Card>
          </Col>
          <Col  xs="6" ys="6" lg="6" xl="6">
          <Card>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
          <div>
            <BootstrapTable data={this.state.drivers} height='310' scrollTop={'Bottom'} deleteRow={ true } selectRow={this.selectRowProp} options={this.options}
            search={true}
            tableStyle={ { border: '#0000FF 2.5px solid' } }
            containerStyle={ { border: '#FFBB73 2.5px solid' } }
            headerStyle={ { border: 'red 1px solid' } }
            bodyStyle={ { border: 'green 1px solid' }}>
              <TableHeaderColumn width="120" dataField="driverLicenseNo" isKey>Licence No</TableHeaderColumn>
              <TableHeaderColumn width="110" dataField="driverName">Driver</TableHeaderColumn>
              <TableHeaderColumn width="140" dataField="driverContact">Contact</TableHeaderColumn>
            </BootstrapTable>
            </div>
            </CardBody>
            </Card>
    
          </Col>
        
        </Row>  
        </Container>
       
        
      </div>
    );
  }
}

export default Forms;
